<template>
	<div id="app">
		<form @submit.prevent="submit">
			<input v-model="id" placeholder="id" value="id">
			<input type="submit">
		</form> 
		<div v-if="items" class="center-api-text">
			<h3 v-if="items.user">{{ items.user }}</h3>
			<p v-if="items.chara">{{ items.chara }} / skill {{ items.skill }}</p>
			<p v-if="items.status">{{ items.status }}</p>
			<p v-if="items.win">{{ items.win }} / {{ items.day }} % </p>
			<div class="tarot-card-list">
				<p v-if="items.status === 'admin'">
					<img :src="'https://syui.cf/ai/chara/' + items.chara + '.gif'"/> 
				</p>
				<p v-else>
					<img :src="'https://syui.cf/ai/chara/' + items.chara + '.png'"/> 
				</p>
				<li>hp : {{ items.hp }}</li>
				<li>attack : {{ items.attack }}</li>
				<li>defense : {{ items.defense }}</li>
				<li>critical : {{ items.critical }}</li>
			</div>
		</div v-else class="center-api-text">
		error id.
	</div>
</div>
</template>

<script>
import axios from 'axios'
export default {
	data() {
		return {
			message: null,
			id: 1,
			url: "https://cardai-cros.herokuapp.com/https://api.syui.cf/users/1",
			items: null
		}
	},
	mounted() {
		axios
			.get(this.url)
			.then(response => { 
				this.items = response.data;
			})
			.catch()
			.finally();
	},
	computed: {
	},
	methods: {
		onError (e) {
			e.target.src = 'https://syui.cf/ai/tarot/tarot_' + ('00' + this.items.skill).slice(-2) + '.webp'
		},
		onErrorfirst (e) {
			e.target.src = 'https://syui.cf/ai/tarot/tarot_' + ('00' + this.items.skill).slice(-2) + '.webp'
		},
		submit() {
			this.url = "https://cardai-cros.herokuapp.com/https://api.syui.cf/users/" + this.id
			axios
				.get(this.url)
				.then(response => { 
					this.items = response.data;
				})
				.catch(response => { 
					this.items = null;
				})
				.finally();
		}
	}
}
</script>

<style>
.center-api-text {
	text-align: center;
}

@media screen and (min-width:1000px) { 
	.center-api-text img {
		width: 320px !important;
	}

	.containerx p img {
		padding: 0 !important;
	}
}
</style>
